<template>
  <div id="app">
    <div id="nav">
      <h1>結帳</h1>
      <!-- <router-link to="/">Home</router-link>| -->
      <div class="step-panel">
        <a
          :class="{
            markUp: $route.name === 'Step1'
          }"
          >1</a
        >
        <div class="line"></div>
        <a
          :class="{
            markUp: $route.name === 'Step2'
          }"
          >2</a
        >
        <div class="line"></div>
        <a
          :class="{
            markUp: $route.name === 'Step3'
          }"
          >3</a
        >
        <div class="line"></div>
        <a
          :class="{
            markUp: $route.name === 'Step4'
          }"
        >
          <i class="fas fa-check"></i>
        </a>
      </div>

      <header>{{ stepTitle }}</header>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  watch: {
    step() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    }
  },
  data() {
    return {};
  },
  computed: {
    step() {
      return this.$route.name;
    },
    stepTitle() {
      switch (this.step) {
        case "Step1":
          return "STEP 1 - 選擇付款方案";
        case "Step2":
          return "STEP 2 - 填寫付款資料";
        case "Step3":
          return "STEP 3 - 確認訂單資訊";
        case "Step4":
          return "付款成功 ！";
      }
      return "error";
    }
  }
};
</script>

