<template>
  <div class="card" :class="{ selected: selected }" @click="select">
    <div class="icon">
      <slot></slot>
    </div>
    <div class="content">
      <h1>{{ head }}</h1>
      <p>{{ paragraph }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    head: {
      type: String,
      required: false,
      default: ':head="string"'
    },
    paragraph: {
      type: String,
      required: false,
      default: ':paragraph="string"'
    },
    payment_method: {
      type: String,
      required: true
    }
  },
  methods: {
    select() {
      this.$store.commit("SET_PAYMENT_METHOD", this.payment_method);
    }
  },
  computed: {
    selected() {
      return this.$store.state.payment_method === this.payment_method;
    }
  }
};
</script>

