    p {
      color: var(--color-contrast);
      opacity: 1;
    }
  }
  &:not(.selected):hover {
    cursor: pointer;
    color: var(--color-hover);
    box-shadow: var(--shadow-hover);
    h1,
    p {
      color: var(--color-hover);
    }
  }
}
.content {
