<template>
  <button @click="routeTo(location)">
    <slot></slot>
  </button>
</template>

<script>
export default {
  props: {
    location: {
      type: String,
      required: false,
      default: "home"
    },
    useVueRouter: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  methods: {
    routeTo(location) {
      if (this.useVueRouter) {
        this.$router.push(location);
      } else {
        window.location.href = location;
      }
    }
  }
};
</script>

